import React, { useState, useEffect } from 'react';
import * as S from 'components/shareButtons/shareButtons.style.jsx';
import shareLinkIcon from 'assets/icons/share-link.png';
import shareKakaoIcon from 'assets/icons/share-kakao.png';
import shareFacebookIcon from 'assets/icons/share-facebook.png';
import Toast from 'components/toast/Toast.jsx';

const ShareButtons = () => {
  const [showToast, setShowToast] = useState(false);

  const copyUrlToClipboard = () => {
    const url = window.location.href;
    navigator.clipboard
      .writeText(url)
      .then(() => {
        setShowToast(true); //  
      })
      .catch((err) => {
        console.error('  .', err);
      });
  };

  useEffect(() => {
    let timer;
    if (showToast) {
      timer = setTimeout(() => {
        setShowToast(false); // 5   
      }, 5000);
    }
    return () => clearTimeout(timer); //     
  }, [showToast]);

  return (
    <S.ShareButtons>
      <S.ShareButton
        src={shareLinkIcon}
        alt=" "
        onClick={copyUrlToClipboard}
      />
      <S.ShareButton src={shareKakaoIcon} alt=" " />
      <S.ShareButton src={shareFacebookIcon} alt=" " />
      {showToast && <Toast text="URL ." />}
    </S.ShareButtons>
  );
};

export default ShareButtons;
